.static {
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Roboto', Roboto, sans-serif;
  font-size: 16px;
}

.static div {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
  height: 0;  
}

.static table {
 	position: relative;
  padding-top: 25px;
  overflow-x:auto;
}
.static iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;  
}
.static p {
  clear: left
}
.static img {
  -o-object-fit: contain;
     object-fit: contain;
  border-radius: 8px;
  padding: 2px;
  max-width: 100%;
  float: left;
  height: auto;
}

img {
  -o-object-fit: contain;
     object-fit: contain;
  padding: 0px;
  max-width: 100%;
  height: auto;
}

p {
  font-size: 16px;
  margin: 5px;
  font-family: 'Roboto', Roboto, sans-serif;
  color: #2F4F4F
}
h1 {
  font-family: 'Roboto', Roboto, sans-serif;
  color: #2F4F4F
}
h5 {
  font-family: 'Roboto', Roboto, sans-serif;
  color: #2F4F4F
}
h4 {
  font-family: 'Roboto', Roboto, sans-serif;
  color: #2F4F4F
}
h6 {
  font-family: 'Roboto', Roboto, sans-serif;
  color: #2F4F4F
}


