.phub-enter {
  opacity: 0.01;
}

.phub-enter.phub-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.phub-leave {
  opacity: 1;
}

.phub-leave.phub-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}
.phub-appear {
  opacity: 0.01;
}

.phub-appear.phub-appear-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.container {
  padding: '.5em .5em .5em .5em'
}

.pop-enter {
  height: 0px;
}

.pop-enter.pop-enter-active {
  height: 640px;
  -webkit-transition: height 3s ease-in;
  -o-transition: height 3s ease-in;
  transition: height 3s ease-in;  
}

.pop-leave {
  height: 640px;
}
.pop-leave.pop-leave-active {
  height: 0px;
  -o-transition: height 300ms ease-in;
  transition: height 300ms ease-in;
  -webkit-transition: height 300ms ease-in;

}

.slideIn-enter {
  -webkit-transform: translate(100%);
      -ms-transform: translate(100%);
          transform: translate(100%);
}
.slideIn-enter.background-enter-active {
  -webkit-transform: translate(0%);
      -ms-transform: translate(0%);
          transform: translate(0%);
  -webkit-transition: -webkit-transform 200ms ease-in-out;
  transition: -webkit-transform 200ms ease-in-out;
  -o-transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}
.slideIn-leave {
  -webkit-transform: translate(0%);
      -ms-transform: translate(0%);
          transform: translate(0%);
}
.slideIn-leave.background-leave-active {
  -webkit-transform: translate(-100%);
      -ms-transform: translate(-100%);
          transform: translate(-100%);
  -webkit-transition: -webkit-transform 200ms ease-in-out;
  transition: -webkit-transform 200ms ease-in-out;
  -o-transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}


